.busket {
  display: flex;
  flex-direction: column;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  transition: all $trans-default;
  background-color: $color-default-white;

  &__name {
    margin: 0 0 94px;
    font-weight: 500;
    font-size: 30px;
    line-height: 88%;
    letter-spacing: -0.04em;
    color: $color-swamp;

    @include vp-767 {
      margin: 0 0 44px;
    }
  }

  &__wrapper-list {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  &__wrapper-list-header {
    display: flex;
    justify-content: space-between;
    margin: 0 0 10px;
  }

  &__products-count {
    font-weight: 400;
    font-size: 14px;
    line-height: 112%;
    color: #1F2020;
  }

  &__clear {
    border: 0;
    outline: 0;
    background-color: $color-transparent;
    padding: 0;
    cursor: pointer;
    font-weight: 300;
    font-size: 14px;
    line-height: 112%;
    color: #1F2020;
    opacity: 0.4;
  }

  &__list {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin: 0;
    padding: 0;
    list-style-type: none;
    row-gap: 12px;
  }

  &__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 13px 0 0 0;

    @include vp-767 {
      display: grid;
      grid-template-areas:
        'img info close'
        'add add add';
      row-gap: 15px;
    }

    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, .1);
    }
  }

  &__item-img {
    min-width: 96px;
    height: 96px;
    margin: 0 8px 0 0;

    img {
      width: 100%;
      height: 100%;
    }

    @include vp-767 {
      grid-area: img;
    }
  }

  &__name-price {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    max-width: 165px;

    @include vp-767 {
      grid-area: info;
    }
  }

  &__product-name {
    font-weight: 300;
    font-size: 16px;
    line-height: 112%;
    letter-spacing: 0.02em;
    color: #1F2020;
  }

  &__price {
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
     color: #1F2020;
  }

  &__counter {
    display: flex;
    align-items: center;
    column-gap: 21px;

    @include vp-767 {
      justify-content: center;
      grid-area: add;
    }
  }

  &__delete,
  &__add {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0;
    outline: 0;
    padding: 4px 12px;
    background-color: $color-white-smoke;
    border-radius: 4px;
    cursor: pointer;
  }

  &__product-count {
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    color: #1F2020;
  }

  &__delete-item,
  &__change-item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    border: 0;
    outline: 0;
    background-color: $color-transparent;
    cursor: pointer;
    padding: 0;
    color: $color-swamp;
    opacity: 0.2;

    @include vp-767 {
      grid-area: close;
    }
  }

  &__change-item {
    svg {
      fill: $color-transparent;
    }
  }

  &__wrapper-summ-buy {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include vp-767 {
      flex-direction: column;
      align-items: flex-start;
      row-gap: 20px;
    }
  }

  &__wrapper-summ {
    display: flex;
    flex-direction: column;
    row-gap: 6px;
  }

  &__summ-name {
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    color: $color-swamp;
  }

  &__summ-count {
    font-weight: 500;
    font-size: 30px;
    line-height: 100%;
    letter-spacing: -0.02em;
    color: $color-swamp;
  }

  &__buy-button {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 240px;
    padding: 20px 40px 21px;
    border: 0;
    outline: 0;
    background-color: $color-padua;
    border-radius: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: $color-swamp;
    cursor: pointer;

    @include vp-767 {
      width: 100%;
    }
  }
}

.modal {
  &--busket {
    .modal__wrapper {
      padding: 0;
      justify-content: flex-end;
    }
    .modal__content {
      width: 100vw;
      max-width: 600px;
      height: 100vh;
    }

    .modal__close-btn {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 32px;
      right: 40px;
      width: 48px;
      height: 48px;
      padding: 0;
      border-radius: 50%;
      border: 1px solid rgba(0, 0, 0, .1);
      background-color: $color-transparent;
      outline: 0;
      cursor: pointer;

      @include vp-767 {
        top: 12px;
        right: 20px;
      }
    }
  }
}
